*{
    margin: 0px;
    padding: 0px;
  }
  
  body{
    overflow-x: hidden;
    background-color: #aaeedd;
  }
  
  header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    margin: 5px auto;
    
  }
  
  header img{
    width: 80px;
  }
  
  header .right-side{
    display: flex;
  }
  
  header .right-side .nav-link-wrapper a{
    padding: 10px;
    margin: 10px;
    text-decoration: none;
    color: black;
  }
  
  .container{
    max-width: 80%;
    margin: 50px auto;
  }
  
  .container .left-side{
    text-align: center;
  }
  
  .container .left-side h1{
    font-size: 60px;
    letter-spacing: 2px;
  }
  
  .container .left-side h1 .green{
    color: green;
    font-family: 'Viga', serif;
  }
  
  .container .left-side p{
    font-size: 20px;
    letter-spacing: 0.5px;
    white-space: pre-wrap;
  }
  
  .container .left-side a{
    display: inline-block;
    background: green;
    padding: 12px 18px 12px 18px;
    text-decoration: none;
    border-radius: 25px;
    color: white;
    margin-top: 15px;
    font-size: 17px;
    font-weight: bold;
  }
  
  .container .right-side{
    text-align: center;
    margin-top: 60px;
  }
  
  .container .right-side img{
    max-width: 50%;
  }
  
  .container .right-side .box{
    position: absolute;
    display: grid;
    right: 0%;
    bottom: 0%;
  }
  
  .container .right-side .box img{
    width: 25px;
    margin: 10px;
  }
  
  .container .right-side .box img.facebook{
    width: 18.5px;
  }
  
  .thumb{/*THUMBS*/
    display: flex;
    margin-top: -60px;
    justify-content: center;
    
  }
  
  .thumb a:hover{
    margin-top: -15px;
    transition: 0.2s;
  }
  
  .thumb img{
    width: 54px;
    margin: 20px;
  }

  /*FOOTER*/
  footer {
    height: 30%;
    display: grid;
    background-color: rgb(164 199 186);
    justify-content: center;
    margin-top: 18px;
    padding: 10px 13px;
    gap: 10px;

  }
  .footer-text {
    color: rgb(23, 102, 72);
    font-size: 18px;
  }
  .footer-text p {
    margin-top: 5px;
  }
  
